.TEST-app-container {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(12, minmax(0, 1fr));
    grid-column-gap: 32px;
    -webkit-column-gap: 32px;
            column-gap: 32px;
}

.TEST-app-nav {
    grid-column-start: 1;
    grid-column-end: 3;
}

.TEST-app-work {
    grid-column-start: 3;
    grid-column-end: 9;
}

.TEST-app-about {
    grid-column-start: 4;
    grid-column-end: 9;
}

.appNavBar {
    text-align: left;
    position: fixed;
    margin-top: 0px;
    padding-top: 40px;
    padding-left: 80px;
    width: 180px;
    height: 100vh;
    display: block;
    /* background-color: lightblue; */
}

.appNavBar .appNavName {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 25px;
    color: black;
    margin-bottom: 1em;
}

.appNavBar a {
    display: block;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 15px;
    color: black;
    margin-top: .5em;
    text-decoration: none;
}

.appNavBar a:hover {
    color: #29FD2F;
}

.home-container {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  grid-column-gap: 32px;
  -webkit-column-gap: 32px;
          column-gap: 32px;
  box-sizing: border-box;
  width: calc(100% - 96px);
  margin-left: 48px;
  margin-right: 48px;
}

.home-item {
  grid-column-start: 3;
  grid-column-end: 13;

  height: calc(100vh - 24px);
  
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-caption {
  text-align: center;
  margin: 0;
  /* padding: 0; */
  padding-top: 2px;

  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  line-height: 146%;
  font-size: 13px;
}


.home-caption span {
  font-style: italic;
}

.home-item img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
  margin-top: 24px;
}

/* media query */

@media screen and (max-width: 600px) {
  .home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;

    /* height: 85vh; */
    height: auto;
    width: 100%;
    padding: 0 10px;

    margin-left:0px;
    margin-right: 0px;
    margin-top: 60px;
  }

  .home-item {
    display: none;
  }

  .mobile-home-item {
    display: flex;
    background-color: transparent;
    max-width: 100%;
    max-height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 0;
    margin-right: 0;
    text-align: center;

  }

  /* .mobile-home-item img {
    object-fit: cover;
    max-width: 100%;
    max-height: 100%;
    margin-top: 24px;

  } */

  .mobile-home-imageRowTwo {
    display: flex;
    background-color: transparent;
    /* max-height: 100%; */


    width: 100%;
    height: auto;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 0px;

    padding-left: 10px;
    padding-right: 10px;


    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 0;
    margin-right: 0;  
  }
  
  .mobile-home-imageRowTwo video {
    object-fit: cover;
    width: 100%;
    height: auto;
    text-align: center;
    /* margin-top: 24px; */
  }

  .mobile-home-caption {
    text-align: center;
    padding: 0;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    line-height: 146%;
    font-size: 15px;
    color: black;
    /* top: 80px; */
    bottom:0;
    margin: 0;
    position: relative;
    background-color: white;
    padding: 0 10px;
    max-width: 100%;
  }

  .mobile-home-caption span {
    font-style:italic;
  }
}



@media screen and (min-width: 601px) and (max-width: 800px) {

  .mobile-home-item {
    display: none;
  }
}

@media screen and (min-width: 801px) {

  .mobile-home-item {
    display: none;
  }

}


