  /* top: 18px; */
  font-family: "Montserrat", sans-serif;
  text-align: left;
  font-weight: 200;
  font-size: 14px;


}

.work-image-row span {
  font-style: italic;
}


@media screen and (max-width: 600px) {

  .work-container {
    margin-top: 48px;
  }

div.work-image-row {
  display: block;
  width: calc(100% - 20px);
  margin-left: 10px;
