.about-container {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  grid-column-gap: 32px;
  -webkit-column-gap: 32px;
          column-gap: 32px;
  box-sizing: border-box;
  width: calc(100% - 96px);
  margin-left: 48px;
  margin-right: 48px;

  font-family: "Montserrat", sans-serif;
  line-height: 1.4em;
  font-weight: 400;
}

.about-text {
  margin-top: 104px;
  grid-column-start: 3;
  grid-column-end: 8;
}

.about-text a {
  text-decoration: none;
  color: magenta;
}

.about-text a:hover {
  color: #29fd2f;
  border-bottom: 1px solid #29fd2f;
}

.about-text h2 {
  font-weight: 800;
  line-height: 1.2em;
}

.about-image-desktop {
  text-align: center;
  grid-column-start: 8;
  grid-column-end: 13;
  margin-top: 16px;
}

.about-presslinks {
  font-weight: 600;
  padding-top: 0;
  padding-bottom: 40px;
}

.about-presslinks p {
  padding: 4px 0;
}

.about-presslinks a {
  text-decoration: none;
  color: magenta;
  border-bottom: 1px solid magenta;
  padding-bottom: 0.5px;
  font-weight: 600;
}

.about-image-desktop img {
  object-fit: cover;
  width: 86%;
  max-height: 100%;
  margin-top: 104px;
}

.about-image-mobile {
  width: 100%;
  height: auto;
  text-align: center;
}

/* MOBILE VERSION */

@media screen and (max-width: 600px) {
  .about-text {
    margin-top: 0;
  }

  .about-container a {
    text-decoration: none;
    border-bottom: 1px solid magenta;
    color: magenta;
    font-weight: 600;
  }

  .about-container img {
    object-fit: cover;
    width: 100%;
    height: auto;
    margin-bottom: 10px;
  }

  .about-container {
    display: block;
    margin: 0;
    width: calc(100% - 20px);
    margin-top: 72px;

    margin-left: 10px;
    margin-right: 10px;
    font-family: "Montserrat", sans-serif;
    line-height: 1.4em;
    font-weight: 400;
    padding-bottom: 32px;
  }

  .about-image-desktop {
    display: none;
  }
}

@media screen and (min-width: 601px) {
  .about-image-mobile {
    display: none;
  }
}

.contact-container {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  grid-column-gap: 32px;
  -webkit-column-gap: 32px;
          column-gap: 32px;
  box-sizing: border-box;
  width: calc(100% - 96px);
  margin-left: 48px;
  margin-right: 48px;
  margin-top: 0;
}

.contact-container section {
  margin-top: 104px;
  grid-column-start: 3;
  grid-column-end: 13;
}

.contact-image-container-desktop {
  grid-column-start: 3;
  grid-column-end: 9;
}

.contact-image-container-desktop img {
  margin-top: 32px;
  margin-bottom: 64px;
  object-fit: cover;
  width: 100%;
  max-height: 100%;
}

.contact-container a:link, 
.contact-container a:visited {
  color: magenta;
  text-decoration: none;
  border-bottom: 1px solid magenta;
}

.contact-container a:hover {
  color: #29fd2f;
  text-decoration: none;
  border-bottom: 1px solid #29fd2f;
}

.contact-mobile-image-container {
  display: none;
}

.contact-container-mobile {
  display: block;
}

/* MOBILE VERSION */

@media screen and (max-width: 600px) {

  .contact-container-desktop {
    display: none;
  }

  .contact-image-container-desktop img {
    display: none;
  }

  .contact-container {
    display: block;
    margin: 0;
    width: 100%;
  }

  .contact-container a:link, 
.contact-container a:visited {
  color: black;
  text-decoration: none;
  border-bottom: 1px solid black;
}

  .contact-container section {
    color: black;
    font-size: 16px;
    line-height: 150%;
    margin-top: 72px;
  }

  .contact-container section a:visited {
    color: black;
  } 
  
  .contact-container-mobile {
    max-width: 100%;
    height: auto;
    text-align: left;
    background-color: transparent;
    line-height: 150%;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 72px;
  }

  .contact-container a {
    font-family: "Montserrat", sans-serif;
    font-size: 16;
    color: black;
  }

  .contact-image-container-mobile img {
    margin-top: 0;
    margin-bottom: 16px;
    object-fit: cover;
    width: 100%;
    max-height: 100%;
  }
.contact-container-mobile a {
  text-decoration: underline;
  border-bottom: 1px solid black;
}

.contact-container-mobile p {
  line-height: .9em;
  font-size: 16px;
}

}

@media screen and (min-width: 601px) {
  .contact-container-mobile {
    display: none;
  }
}
/* desktop menu navigation */


.desktop-nav-container {
  font-family: "Montserrat", sans-serif;
  /* font-family: "system-ui", "Roboto", "Montserrat", sans-serif; */
  width: 180px;
  height: 100%;
  margin-top: 28px;
  margin-left: 48px;
  position: fixed;
  display: inline-block;
}

.desktop-nav-list {
  list-style-type: none;
  padding: 0;
}

.desktop-nav-list a {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: black;
  text-decoration: none;
  line-height: 170%;
}

.desktop-nav-list a:hover {
  color: #29fd2f;
}

.desktop-nav-name {
  font-weight: 800;
  font-size: 30px;
  line-height: 130%;
  letter-spacing: 0%;
  margin-top: 0;
  margin-bottom: 24px;
  color: #000;
}

/* Mobile menu navigation */

.mobile-nav-list {
  background-color: white;
  list-style-type: none;
  text-align: center;

  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 100;
  left:0;
  top: 0;
  overflow-x: hidden;
  transition: 0.5s;
}

.mobile-nav-list li {
  background-color: white;
  list-style-type: none;
  margin-left: -48px;
  font-size: 2em;
  padding: 0;

  top: 25%;
  position: relative;
  width: 100%;
  text-align: center;
}

.mobile-nav-list a {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  color: black;
  text-decoration: none;
  line-height: .95em;

  position: relative;
  /* padding: 3vw 5vw; */
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.mobile-nav-name h1 {
  position: absolute;
  top: 0;
  left: 20px;
  z-index: 100;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  font-size: 30px;
  line-height: 130%;
  margin-top: 16px;
}

/* Icon */

.mobile-menu-button-icon {
  position: absolute;
  top: 6.5px;
  right: 12px;
  width: 50px;
  height: 50px;
  background: transparent;
  border: none;
  z-index: 200;
}

/* Resizing */

@media (max-width: 599px) {

  .desktop-nav-container {
    display: none;
  }

  .nav-hamburger-icon {
    display: inline-block;
  }

  button.mobile-button {
    display: block;
    position: fixed;
  }

  .mobile-nav-list a {
    /* position: fixed; */
    /* background-color: #eeedf2; */
    color: black;
  }

  .mobile-nav-name {
    display: inline-block;
  }
}

@media (min-width: 600px) {
  .nav-hamburger-icon {
    display: none;
  }

  .desktop-nav-container {
    display: inline-block;
  }

  .mobile-button {
    display: none;
  }

  .mobile-nav-name {
    display: none;
  }
}

